body {
  background-color: #008b8b;
}

#title {
  color: #ff0;
}

button {
  width: 100%;
  height: 30px;
  background-color: #faebd7;
}

.navbar {
  z-index: 1;
  background-color: #dde3ae;
  flex-flow: row;
  justify-content: center;
  gap: 100px;
  display: flex;
  position: sticky;
  top: 0;
}

.navbar a {
  color: #031102;
  font-size: 50px;
  text-decoration: none;
  transition: all .2s;
}

.navbar a:hover {
  color: #eb0a0a;
}

.navbarCounter {
  color: red;
  text-align: center;
  background: #eff6fb;
  border: 2px solid #666;
  border-radius: 50%;
  padding: 4px;
  font-size: 15px;
  font-weight: bold;
}

.img-size {
  width: 200px;
}

.productsContainer {
  width: 54.5%;
  text-align: center;
  flex-flow: wrap;
  gap: 2px;
  margin: auto 25% auto auto;
  display: flex;
}

.productsContainer div {
  width: 200px;
  border: 1px solid #000;
}

.productsContainer div button:active {
  background-color: orange;
}

#cartDisplayTitle {
  color: #000;
}

.cartDisplay {
  box-sizing: border-box;
  text-align: center;
  float: right;
  width: 380px;
  background-color: green;
  border: 2px solid #000;
  margin-right: 60px;
}

.cartDisplay h2 {
  color: #fff8dc;
}

#shoppingCartTitle {
  color: #000;
}

.shoppingCartText {
  width: 30%;
  text-align: center;
  background-color: green;
  border: 2px solid #000;
  margin: 100px auto auto;
}

.shoppingCartText h2 {
  color: #fff8dc;
}

.shoppingCartText button {
  width: 200px;
  background-color: orange;
}

@media screen and (width <= 1536px) {
  .img-size {
    width: 150px;
  }

  .cartDisplay {
    width: 250px;
  }
}

@media screen and (width <= 400px) {
  .img-size {
    width: 50px;
  }

  .navbar a {
    font-size: 20px;
  }

  .productsContainer {
    width: 250px;
    font-size: 10px;
  }

  .productsContainer div {
    width: 100px;
    border: 1px solid #000;
  }

  button {
    width: 100px;
    font-size: 10px;
  }

  .cartDisplay {
    width: 60px;
    margin-right: 0;
    font-size: 10px;
  }

  .shoppingCartText {
    width: 70%;
  }

  .shoppingCartText button {
    width: 120px;
    background-color: orange;
  }
}

